<template>
  <b-container class="send-offer-container">
    <b-row class="submit-offer-header justify-content-center">
      <svg-icon
        v-if="!desktopView"
        class="back"
        @click="$emit('close')"
        name="close"
        height="24px"
        width="24px" />
      <div class="header-icon">
        <svg-icon
          class="back"
          @click="$emit('close')"
          name="HandWaving"
          height="32px"
          width="32px" />
      </div>
      <h1 class="font-weight-semi font-size-large mt-2">
        {{ $t('offer.submit_offer') }}
      </h1>
    </b-row>

    <b-row class="properties-main-container">
      <b-col cols="12" class="mt-2">
        <h1 class="gray font-size-semi-large">
          {{ $t('marketplace.select_your_property') }}
        </h1>
        <div class="properties-container col-12">
          <div class="properties-scroll-row">
            <div
              :class="[
                offerForm.propertyId == property.id
                  ? ' selectedProperty property-card'
                  : 'property-card',
              ]"
              v-for="property in userProperties"
              :key="property.id"
              @click="changeSelectedProperty(property.id)">
              <b-row>
                <!-- :src="generateSrc(property.images[0])" -->
                <img
                  src="@/assets/images/home/lanlord-rating-landing.jpg"
                  alt=""
                  srcset="" />
                <span class="radio"></span>
              </b-row>
              <b-row>
                <div class="property-title">
                  {{ property.name }}
                </div>
              </b-row>
            </div>
          </div>
        </div>
      </b-col>
    </b-row>
    <b-row class="offer-price-container">
      <b-col cols="12" class="mt-4">
        <h5 class="font-size-normal">
          {{ $t('form.price') }}
          <span class="font-size-smaller light-gray"
            >({{
              selectedRequest.term === 1
                ? $t('common.monthly')
                : $t('common.daily')
            }})</span
          >
        </h5>
        <AppInput
          :placeholder="$t('marketplace.price_placeholder')"
          v-model="offerForm.price"
          type="text"></AppInput>
      </b-col>
    </b-row>

    <b-row class="starting-date-container">
      <b-col cols="12" class="mt-3">
        <h5 class="font-size-normal">
          {{ $t('form.postingRequest.stayingDetails.starting_date') }}
        </h5>
        <b-form-datepicker
          :date-format-options="dateFormatOptions"
          id="datepicker"
          v-model="offerForm.startingDate"
          @input="changeDate"
          :placeholder="$t('common.date')"
          today-button
          reset-button
          close-button
          locale="en"
          aria-describedby="inputcheck-in-feedback" />
      </b-col>
      <b-col cols="12" class="mt-4 mb-2">
        <h5 class="font-size-normal">
          {{ $t('form.postingRequest.stayingDetails.duration') }}
        </h5>
      </b-col>
      <b-col cols="12">
        <b-row>
          <b-col cols="3">
            <b-form-input type="number" class="mx-2 w-100" v-model="formatedDuration.num"></b-form-input>
          </b-col>
          <b-col cols="9">
            <b-select class="w-100" v-model="formatedDuration.type" :options="durationOptions"></b-select>
          </b-col>
        </b-row>
      </b-col>
    </b-row>

    <b-row class="mb-4">
      <b-col cols="12" class="mt-4">
        <b-button
          variant="primary"
          type="button"
          class="w-100 d-block"
          v-text="$t('offer.confirm_offer')"
          @click="confirmOrder">
        </b-button>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { submitOffer, fetchUserProperties } from '@/api/marketplace';
import { generateServerImageUrl } from '@/helpers/images';
export default {
  name: 'marketplace-send-offer',
  props: {
    selectedRequest: {
      type: Object,
      default: () => {},
    },
    desktopView: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      offerForm: {
        propertyId: null,
        price: this.selectedRequest.budget,
        startingDate: this.selectedRequest.move_in_date || '',
        selectedTerms: [],
        duration: this.selectedRequest.duration,
        note: '',
      },
      userProperties: [],
      durationOptions: ['day', 'month', 'year'],
      dateFormatOptions: {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
      },
    };
  },
  computed: {
    transformUserConditions() {
      return this.selectedRequest.conditions.map((condition) => ({
        value: condition.id,
        text: condition.name,
      }));
    },
    formatedDuration(){
      let { duration } =  this.offerForm;
      let splitedDuration = duration.split(' ');
      let offerData = {
        num: parseInt(splitedDuration),
        type: splitedDuration[1],
      };
      if(splitedDuration[1].indexOf('day') >= 0){
        offerData.type = 'day';
        offerData.min = 1;
        offerData.max = 31;
      }else if(splitedDuration[1].indexOf('month') >= 0){
        offerData.type = 'month';
        offerData.min = 1;
        offerData.max = 12;
      }else if(splitedDuration[1].indexOf('year') >= 0){
        offerData.type = 'year';
        offerData.min = 1;
        offerData.max = 10;
      }
      return offerData;
    },
  },
  created() {
    this.getUserProperties();
    console.log(this.formatedDuration);
  },
  methods: {
    changeSelectedProperty(id) {
      this.offerForm.propertyId = id;
    },
    generateSrc(file) {
      if (!file) return '';
      if (typeof file === 'string')
        return generateServerImageUrl({
          $config: this.$config,
          url: file,
        });

      return URL.createObjectURL(file);
    },
    changeDate(value) {
      this.offerForm.startingDate = value;
    },

    clearForm() {
      this.offerForm = {
        ...this.offerForm,
        propertyId: null,
        price: null,
        startingDate: '',
        selectedTerms: [],
        note: '',
      };
    },
    transformUserTerms() {
      return this.selectedRequest.conditions.map((condition) => {
        if (this.offerForm.selectedTerms.indexOf(condition.id) >= 0) {
          return {
            condition_id: condition.id,
            status: 1,
          };
        }

        return {
          condition_id: condition.id,
          status: 0,
        };
      });
    },

    async getUserProperties() {
      try {
        const { Data } = await fetchUserProperties();

        this.userProperties = Data.collection;
        console.log(this.userProperties);
        if (this.userProperties.length > 0) {
          this.offerForm.propertyId = this.userProperties[0].id;
        }
      } catch (error) {}
    },

    async confirmOrder() {
      // alert(1)
      this.$emit('colseSideBar');
      this.$emit('close');
      // alert(2)
      const { price, propertyId, startingDate, note, duration } = this.offerForm;

      try {
        const payload = {
          property_id: propertyId,
          price,
          move_in_date: startingDate,
          note,
          duration: `${this.formatedDuration.num} ${this.formatedDuration.type}`,
          conditions: this.transformUserTerms(),
        };
        console.log(payload);
        const response = await submitOffer(this.selectedRequest.id, payload);
        this.clearForm();
        let body = response.Data.message;
        this.$bvToast.toast(body, {
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          bodyClass: ['toast-body'],
          headerClass: ['toast-header'],
          autoHideDelay: 2000,
          noCloseButton: true,
        });
      } catch (error) {
        if (error.response && error.response.data) {
          let errorMessage = error.response.data.Data.message;
          this.$bvToast.toast(`${errorMessage} `, {
            title: 'Error',
            toaster: 'b-toaster-top-center',

            autoHideDelay: 5000,
            variant: 'danger',
            appendToast: true,
          });
        }
        this.clearForm();
      }
    },
  },
};
</script>

<style lang="scss">
.submit-offer-header {
  flex-direction: column;
  align-items: center;
  padding: 25px;
  h1 {
    text-transform: capitalize;
    color: #000;
  }
  .header-icon {
    width: 60px;
    height: 60px;
    background-color: white;
    border-radius: 10px;
    text-align: center;
    line-height: 60px;
  }
}
.properties-main-container {
  background-color: white;
  padding: 10px 0px;
  .properties-container {
    height: fit-content;
    margin-top: 25px;
    overflow-x: scroll;
    &::-webkit-scrollbar {
      width: 0px;
    }
    .properties-scroll-row {
      gap: 10px;
      display: inline-flex;
      margin-left: -15px;
    }

    .property-card {
      width: 150px;
      height: 110px;
      border-radius: 20px;
      border: solid 1px rgb(221, 220, 220);
      padding: 15px 30px;
      position: relative;

      cursor: pointer;
      img {
        width: 70%;
        height: 54px;
        border-radius: 10px;
      }
      .radio {
        width: 15px;
        height: 15px;
        position: absolute;
        top: 15px;
        right: 15px;
        border-radius: 100%;
        border: solid 1px #000;
        background-color: white;
      }
      .property-title {
        font-size: 10px;
        font-weight: bold;
        text-transform: capitalize;
        margin-top: 10px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
    .selectedProperty {
      border: solid 1px black;
      .radio {
        width: 20px;
        height: 20px;
        position: absolute;
        top: 15px;
        right: 15px;
        border-radius: 100%;
        border: solid 4px #ffff;
        border-style: double;
        background-color: blue;
      }
    }
  }
}

.send-offer-container {
  .offer-price-container {
    background-color: white;
    margin-top: 10px;
    input {
      border-radius: 10px !important;
    }
  }
  .starting-date-container {
    background-color: white;
    padding: 0px 0px 20px 0px;
    margin-top: 10px;
  }
}
.send-offer-container .custom-control.custom-checkbox {
  width: 100%;

  margin-bottom: 10px;
  padding: 10px;
  border-radius: 4px;
  background: linear-gradient(
      0deg,
      rgba(38, 45, 61, 0.04),
      rgba(38, 45, 61, 0.04)
    ),
    #ffffff;

  span {
    color: #0f256e !important;
    font-weight: bold;
    margin-left: 1.5rem;
    margin-bottom: 0 !important;
    [dir='rtl'] & {
      margin-right: 1.5rem;
    }
  }
}
.send-offer-container .custom-control-label span {
  color: #0f256e !important;
  margin-left: 1.5rem;
  margin-bottom: 0 !important;
}
.send-offer-container .custom-control-label::before {
  left: 0;
  [dir='rtl'] & {
    right: 0 !important;
  }
}
.send-offer-container img.property-image {
  width: 64px;
}
</style>
