import request from '@/helpers/axios-port';

/**
 * GET: fetch requests data
 */
export function fetchMarketPlaceData(query) {
  const fullQuery = Object.keys(query)
    .map((key) => `${key}=${query[key]}`)
    .join('&');

  const url = `/requests${query && `?${fullQuery}`}`;
  return request.$get(url);
}

/**
 * GET: fetch user properties
 */
export function fetchUserProperties() {
  const url = `/user/getMyProperties`;
  return request.$get(url);
}

/**
 * POST: submit offer
 */
export function submitOffer(requestId, data) {
  console.log(data);
  const url = `/user/requests/${requestId}/send-offer`;
  return request.$post(url, data);
}
