<template>
  <SendOffer
    :selectedRequest="selectedRequest"
    v-if="showOfferForm"
    @close="showOfferForm = false"
    @colseSideBar="$emit('colseSideBar')" />

  <b-container v-else>
    <b-row>
      <b-row cols="12" class="header-section">
        <div class="d-flex justify-content-start align-items-start flex-column">
          <h4 class="font-weight-bold font-size-semi-large">
            {{ selectedRequest.name }}
          </h4>
          <span class="user_name">
            {{ '@' + selectedRequest.user_name }}
          </span>
          <p
            class="title-3-content text-start mt-3 font-size-small"
            v-if="dir === 'ltr'">
            <!-- this is for Making it english and arabic -->
            Add Post requirements here, and wait for home owners to get back to
            you with offers.
          </p>

          <p class="title-3-content text-start mt-3 font-size-small" v-else>
            <!-- this is for Making it english and arabic -->
            أضف متطلبات النشر هنا ، وانتظر حتى يعود أصحاب المنازل إليها مع
            العروض.
          </p>
        </div>
      </b-row>
    </b-row>
    <!-- Staying details section -->
    <b-row class="staying-details">
      <b-col cols="12">
        <h5 class="font-weight-bold">
          {{ $t('form.postingRequest.preview.staying_details') }}
        </h5>
        <b-row>
          <!-- calendy card -->
          <b-col cols="6">
            <SingleCard
              SvgName="calendar"
              :Title="selectedRequest.duration"
              :Content="
                'From' +
                $dateFns.format(
                  new Date(selectedRequest.move_in_date),
                  'dd MMM yy'
                )
              " />
          </b-col>
          <!-- price card -->
          <b-col cols="6">
            <SingleCard
              SvgName="price"
              :Title="selectedRequest.budget + $t('common.egp')"
              :Content="
                selectedRequest.term === 1
                  ? $t('common.per_month')
                  : $t('common.per_night')
              " />
          </b-col>
        </b-row>
      </b-col>
    </b-row>
    <!-- Your terms
 section -->
    <b-row>
      <div class="staying-container col-12">
        <div class="title-section">
          <h5 class="font-size-semi-large gray">
            {{ $t('form.postingRequest.preview.your_terms') }}
          </h5>
        </div>
        <b-row>
          <div
            class="details-item mt-2 align-items-center"
            v-for="condition in selectedRequest.conditions"
            :id="condition.id"
            :key="condition.id">
            <svg-icon name="duration" width="24" height="24" />
            <h3 :class="['font-size-smaller', dir === 'rtl' ? 'mr-2' : 'ml-2']">
              {{ condition.name }}
            </h3>
          </div>
        </b-row>
      </div>
    </b-row>

    <b-row>
      <div class="details-container col-12">
        <b-row>
          <div class="arrive-section">
            <h5 class="font-size-semi-large gray">
              {{ $t('form.postingRequest.preview.arrive') }}
            </h5>
            <h4 class="font-size-semi gray m-0">
              {{ $t('form.postingRequest.preview.early_check') }}
            </h4>
          </div>
        </b-row>
      </div>
      <b-col cols="12" class="m-0 p-0">
        <div class="details-container col-12 staying_details">
          <b-row>
            <h4 class="font-size-semi gray m-0">
              {{ $t('form.postingRequest.SelectPreferences.minimum_contract') }}
            </h4>
            <span> 1 </span>
          </b-row>
        </div>
        <div class="details-container col-12 staying_details">
          <b-row>
            <h4 class="font-size-semi gray m-0">
              {{ $t('form.postingRequest.SelectPreferences.eviction_notice') }}
            </h4>
            <span> 1 </span>
          </b-row>
        </div>
        <div class="details-container pb-0 col-12 staying_details">
          <b-row class="eviction_notice">
            <h4 class="font-size-semi gray m-0">
              {{
                $t(
                  'form.postingRequest.SelectPreferences.maximum_annual_increase'
                )
              }}
            </h4>
            <p>24%</p>
          </b-row>
        </div>
      </b-col>
    </b-row>

    <b-row>
      <b-col cols="12" class="m-0 p-0">
        <div class="details-item details-container mt-2 pt-2">
          <h3 class="font-size-small font-weight-semi mt-1">
            {{ $t('form.postingRequest.preview.furnishing_status') }}
          </h3>
          <h3
            v-for="(furnish, index) in selectedRequest.furnishing_status"
            :key="index"
            class="font-size-small font-weight-semi-small">
            {{ furnish }}
          </h3>
        </div>

        <div class="details-container minimum-rooms mt-2 pt-2">
          <b-row class="p-0 mb-4">
            <h3 class="font-size-semi gray m-0">
              {{ $t('form.postingRequest.preview.minimum_rooms') }}
            </h3>
            <span> 1 </span>
          </b-row>
        </div>

        <!-- <div class="details-item details-container mt-2 pt-4">
          <h3 class="font-size-small font-weight-semi mt-1">
            {{ $t('form.postingRequest.preview.property_types') }}
          </h3>
          <br />
          <b-row class="m-0 prop-type">
            <span v-for="propType in propertyType" class="chip font-size-small font-weight-semi-small" :key="propType">
              {{ displayPropertyType }}
            </span>
          </b-row>
          <br />
        </div> -->
      </b-col>
    </b-row>

    <b-row
      v-show="mode != 'desktop'"
      :style="{ position: 'fixed', top: '88%' }"
      class="bg-light mb-4 w-100">
      <b-col cols="4" class="mt-4">
        <b-button
          variant="outline"
          type="button"
          class="w-100 d-block px-1"
          v-text="$t('marketplace.back')"
          @click="$emit('back')">
        </b-button>
      </b-col>
      <b-col cols="8" class="mt-4">
        <b-button
          variant="primary"
          type="button"
          class="w-100 d-block"
          v-text="$t('marketplace.interested')"
          @click="handleShowOffer">
        </b-button>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import SendOffer from './SendOffer.vue';
import SingleCard from './SingleCard.vue';
import { createNamespacedHelpers } from 'vuex';
const { mapActions: mapAuthActions } = createNamespacedHelpers('AuthModule');
const { mapState } = createNamespacedHelpers('UserModule');
export default {
  name: 'marketplace-item',
  components: {
    SendOffer,
    SingleCard,
  },
  props: {
    selectedRequest: {
      type: Object,
      default: () => {},
    },
    mode: '',
  },
  data() {
    return {
      showOfferForm: false,
    };
  },

  computed: {
    ...mapState(['user']),
    displayPropertyType() {
      if (this.selectedRequest.property_type) {
        return this.selectedRequest?.property_type[`name_${this.$i18n.locale}`];
      }
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
  },
  methods: {
    ...mapAuthActions(['showAuthModal']),
    handleShowOffer(value) {
      if (this.user) {
        this.showOfferForm = true;
      } else {
        this.showAuthModal();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.details-container {
  background: #ffffff;
  padding: 0px 25px 20px;
  overflow: hidden;
  .prop-type {
    border: 0px !important;
  }
  .title-section {
    margin-bottom: 15px;
  }
  .row {
    gap: 20px;
  }
  .details-item {
    width: calc((100% / 2) - 10px);
    text-align: center;
    padding: 15px;
    border: solid 1px #e3e5e5;
    border-radius: 10px;
    h3 {
      text-align: center;
      margin: 15px 0px 0px;
      color: black;
      font-weight: bold;
    }
    span {
      font-size: 12px;
      color: #48535b;
    }
  }
}
.staying-container {
  background: #ffffff;
  padding: 20px 25px;
  overflow: hidden;
  margin-top: 10px;
  .title-section {
    margin-bottom: 15px;
  }
  .row {
    margin: 0px;
    gap: 20px !important;
    .details-item {
      display: inline-flex !important;
    }
  }
}

.staying-details {
  background-color: #fff;
  padding: 25px 10px;
}
.staying-container,
.details-container {
  .row {
    margin: 0px;
    border-bottom: solid 1px #f2f4f5;
    padding-bottom: 20px;
    align-items: center;
  }
  .eviction_notice {
    border: 0px;
  }
}
::v-deep() .details-item-s1 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1.5rem 1rem 0.5rem 1rem;
  background: #ffffff;
  border: 1px solid #e3e5e5;
  border-radius: 16px;
}
.staying_details,
.minimum-rooms {
  .row {
    justify-content: space-between;
  }
  span {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    border: solid 1px #f2f4f5;
    line-height: 40px;
    text-align: center;
  }
}
.minimum-rooms {
  .row {
    border: 0px !important;
  }
  h3 {
    margin: 0px;
  }
}
.icon {
  max-height: 20px;
}

::v-deep() .custom-control-label::before {
  left: 0;
}

.btn-outline {
  border: 1px solid $primary;
  color: $primary;
  font-weight: bold;
}
.header-section {
  background-color: #f7f9fa;
  padding: 20px;
}
::v-deep().title-3 {
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  color: #111111;
}
::v-deep() .title-3-content {
  line-height: 16px;
  color: #48535b;
}
.chip {
  padding: 10px 15px;
  background-color: #79b7ff5b;
  border-radius: 20px;
  color: #0078ff;
  font-weight: bold;
  font-size: 12px;
}
</style>
